
.widget {
    width:90%;
    margin:50px auto 0 auto;
    border:4px solid rgba(10,10,10,0.05);
    padding:10px;
    border-radius:10px;
    -webkit-box-shadow: 10px 10px 45px -20px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 45px -20px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 45px -20px rgba(0,0,0,0.75);

    a {
        color:#9b9b9b;
        text-decoration:none;
        padding:10px 0 10px 10px;
        display:inline-block;
        width: 90%;
        text-align: right;
    }

    i {
        color: #9b9b9b;
        padding:10px 16px;
        float:right;
        line-height:1.5;
    }

    i:hover {
        cursor: pointer;
        color:#444;
    }

    a:hover {
        color:#444
    }

    ul {
        list-style-type:none;
        margin:0;
        padding:0;
        background-color:white;
    }

    ul li:first-child .fa-angle-down {
        -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }

    ul {
        line-height:1.5;
    }

    li {
        border-top:1px solid #ccc;
    }

    .widget > ul li:first-child
    li a:first-child {
        display:inline-block;
    }
}

.widget-title {
    font-weight:bold;
    text-transform:uppercase;
    color:#636363;
    padding-bottom:10px
}

.hide {
    display:none;
}

/* Box-sizing -*/
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}


/* Grid */

.row {
    width: 100%;
    max-width: 960px;
    min-width: 680px;
    margin: 0 auto;
    background: #fff;
}

/* Micro-clearfix */
.row:before,
.row:after {
    content: " ";
    display: table;
}

.row:after {
    clear: both;
}

.row {
    *zoom: 1;
}

.image {
    width: 90%;
}

[class*='column-'] {
    float: left;
    min-height: 1px;
    padding: 0 15px;
    background: #fff;
}

.custom-column {
    margin: 3px;
    background-color: red;
}

.row-1-3 {
    width: 33.33%;
}

.row-1-2 {
    width: 50%;
}

.column-1 {
    width: 100%;
}

[class*="column-"] + [class*="column-"]:last-child {float: left;}

@media only screen and (max-width:640px) {
    .row {
        width: auto;
        min-width: 0;
        margin-left: 0;
        margin-right: 0;
    }

    .image {
        width: 70%;
    }

    [class*="column-"] {
        width: auto !important;
        float: none;
    }

    [class*="column"] + [class*="column"]:last-child {float: none;}

    [class*="column-"]:before, [class*="column-"]:after {
        content:"";
        display:table;
    }

    [class*="column-"]:after {clear: both;}
}

